<!--  -->
<template>
   <div id="customized-seach">
     <search-header :hasBack="hasBack" v-on:change="getSearchKey">
      <!-- <router-link :to="{ name : 'Index'}" slot="icon">
            <div class="message" @click="toMessage">
              <img src="../../assets/common_btn_message@2x.png"/>
              <span class="point">0</span>
            </div>
          </router-link> -->
          <span class="search-btn" slot="icon" @click="searchClick">搜素</span>
    </search-header>
    <div class="store-list">
      <div class="list-item">
        <div class="item-img fl-left">
          <img src="../../assets/pic.png">
          <div class="item-count">13</div>
        </div>
        <div class="item-info fl-left">
          <h4>巴黎欧莱雅官方旗舰店</h4>
          <p>定制商品数：2560</p>
        </div>
      </div>

    </div>
    <div class="goods-item">
      <div class="goods-img">
        <img src="../../assets/GoodsList-pic.jpg" alt="" />
      </div>
      <div class="goods-info">
        <div class="goods-name">123</div>
        <div class="goods-content">
          <div class="goods-price">
            <em class="price-icon">￥</em>123</div>
          <div class="goods-comments">
            <!--<span class="goods-label">{{item.tags}}</span>-->
            <span class="goods-comment">123 条评价</span>
            <span class="goods-rate fl-right">123 好评</span>
          </div>
        </div>
        <!--</router-link>-->
      </div>
    </div>
  </div>
</template>

<script>
import SearchHeader from "@/components/Header/SearchHeader";

export default {
  data() {
    return {
      hasBack: true,
      hasSeted: true,
      isShow: true,
      searchKey: "123",
      isSearch: true
    };
  },

  components: {
    SearchHeader
  },

  computed: {},

  created() {},

  methods: {
    getSearchKey(val) {
      this.searchVal = val;     
    },
    //搜索按钮
    searchClick() {
      this.historyArr.push(this.searchVal);
      let newSet = new Set(this.historyArr);
      this.historyArr = Array.from(newSet);
      localStorage.setItem("searchVal", JSON.stringify(this.historyArr));
      let storageArr = localStorage.getItem("searchVal");
      this.toGoodsList(this.searchVal);
    }
  }
};
</script>
<style lang='scss' scoped>
@import "../../sass/baseSass.scss";

#customized-seach {
  .search-btn {
    display: inline-block;
    font-size: 0.4rem;
    border-radius: 0.4rem;
    width: 1.33rem;
    height: 0.8rem;
    line-height: 0.8rem;
    text-align: center;
    background: #fff;
    color: #de7fef;
    margin-left: 0.27rem;
  }
  .store-list {
    background: #fff;

    .list-item {
      width: box(750);
      height: box(220);
      border-bottom: solid #ccc box(1);
      padding: box(30);

      .item-img {
        width: box(220);
        height: box(160);
        position: relative;

        img {
          width: box(220);
          height: box(160);
          border-radius: box(8);
        }
        .item-count {
          min-width: box(30);
          height: box(30);
          background-color: #fe4436;
          box-shadow: 1px 2px 7px 0px rgba(0, 0, 0, 0.35);
          border-radius: box(15);
          position: absolute;
          bottom: box(10);
          right: box(10);
          color: #fff;
          line-height: box(30);
          text-align: center;
          font-size: box(24);
          padding: 0 box(9);
        }
      }

      .item-info {
        width: box(440);
        height: box(160);
        position: relative;
        margin-left: box(30);
        color: #1a1a1a;

        h4 {
          font-size: box(30);
        }
        p {
          font-size: box(28);
          position: absolute;
          bottom: 0;
          left: 0;
        }
      }
    }
  }
  .goods-item {
    display: flex;
    height: 3.47rem;
    padding: 0.27rem 0.4rem;
    border-bottom: 1px solid $border-color;
    .goods-img {
      width: 2.93rem;
      height: 2.93rem;
      border-radius: 8px;
      > img {
        width: 2.93rem;
        height: 2.93rem;
        border-radius: 8px;
      }
    }
    .goods-info {
      position: relative;
      flex: 1;
      line-height: 1;
      margin-left: 0.35rem;
      text-align: left;
      .goods-name {
        font-size: 0.37rem;
        line-height: 1.286;
        @include textflow-two;
        color: $text-color;
      }
      .goods-content {
        position: absolute;
        width: 100%;
        bottom: 0.07rem;
      }
      .goods-price {
        font-size: 0.45rem;
        font-weight: bold;
        padding-top: 0.93rem;
        color: $price-color;
        .price-icon {
          font-size: 0.37rem;
        }
      }
      .goods-comments {
        font-size: 0.32rem;
        margin-top: 0.24rem;
        color: $text-color2;
        .goods-label {
          @include goodslabel;
        }
      }
    }
  }
}
</style>


